<template>
    <yd-layout>
        <yd-navbar title="内部协办" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="#" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContent handle_main new_label" v-show="mainFlag">
            <div class="xieban">
                <yd-flexbox direction="vertical">
                    <yd-flexbox-item>
                        <yd-flexbox>
                            <div class="hint" v-text='demandname'></div>
                        </yd-flexbox>
                        <yd-flexbox v-if="true">
                            <div class="btn_label">
                                <span class="jijian" v-text="returnYijian(servicetype)"></span>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
                <div class="other_content fuyan_content">
                    <div class="fuyan_box">送审附言</div>
                    <h3 class="fuyan" v-text="fuyan"></h3>
                </div>
                <div class="other_content" v-if="xiebanFlag">
                    <h2 class="circle_box">本部门处理结果单</h2>
                    <yd-flexbox>
                        <div class="range">协办方：</div>
                        <div class="range_txt" v-text="assistantdeptname"></div>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="range">经办人：</div>
                        <div class="range_txt" v-text="assistantname"></div>
                    </yd-flexbox>
                    <yd-flexbox direction="vertical">
                        <div class="range">协办结果描述：</div>
                        <div class="text_box">
                            <p class="line6n" ref="line6"></p>
                            <p id="xiebanText" ref="textBox" class="detail_text" :class="{'line6': line6flag}" v-html="transferString(resultdescribe)"></p>
                            <span class="openOver1" v-show="resultdescribe != '' && over1" @click="textHidden = !textHidden; line6flag = !line6flag" :class="textHidden ? 'open' : 'close'">{{ textHidden ? '展开全部' : '收起'}}</span>
                        </div>
                    </yd-flexbox>
                    <yd-flexbox v-if="resultattch.length > 0" direction="vertical">
                        <div class="range">处理结果附件：</div>
                        <ul class="list">
                            <li class="file" v-for="(item, index) in resultattch" @click="queryText(file_url.chuli_file, index)" v-text="item"></li>
                        </ul>
                    </yd-flexbox>
                </div>
                <div class="find_more" v-if="xuqiuFlag == false && xiebanFlag == true" @click="xuqiuFlag = true; seeMore()">点击查看需求信息</div>
            </div>
            <div class="xuqiu" v-if="xuqiuFlag || xiebanFlag == false">
                <div class="other_content">
                    <ul class="tab_ul">
                        <li @click="tabflag = true" :class="{active: tabflag == true}">需求描述</li>
                        <li @click="tabflag = false" :class="{active: tabflag == false}">查看附件</li>
                    </ul>
                    <div v-if="tabflag">
                        <yd-flexbox>
                            <div class="range">需求部门：</div>
                            <div class="range_txt" v-text="demandside"></div>
                        </yd-flexbox>
                        <yd-flexbox direction="vertical">
                            <div class="range">需求描述：</div>
                            <div class="text_box circle_box">
                                <p class="line10n" ref="line10"></p>
                                <p ref="textBoxa" class="detail_text" :class="{'line10': line10flag}" v-html="transferString(assistdescribe)"></p>
                                <span class="" v-show="assistdescribe != '' && over2" @click="textHidden1 = !textHidden1; line10flag = !line10flag" :class="textHidden1 ? 'open' : 'close'">{{ textHidden1 ? '展开全部' : '收起'}}</span>
                            </div>
                        </yd-flexbox>
                        <yd-flexbox direction="vertical">
                            <div class="range">协办部门：</div>
                            <div class="text_box circle_box">
                                <p v-if="textHidden2" class="detail_text" v-text="getText(personnames, 43)"></p>
                                <p v-else class="detail_text" v-text="personnames"></p>
                                <span v-show="personnames != '' && getLength(personnames) > 43" @click="textHidden2 = !textHidden2" class="open1">{{ textHidden2 ? '展开全部' : '收起'}}</span>
                            </div>
                        </yd-flexbox>
                        <yd-flexbox>
                            <div class="range">联系人：</div>
                            <div class="range_txt" v-text="linkman"></div>
                            <a v-for="item in phone" :href="`tel:${item}`" class="range_txt blue">{{ "&nbsp;&nbsp;" +item}}</a>
                        </yd-flexbox>
                    </div>
                    <div v-else>
                        <yd-flexbox direction="vertical">
                            <div class="range">需求附件：</div>
                            <ul class="list top_ul circle_box">
                                <li class="file" v-for="(item, index) in requestAttach" @click="queryText(file_url.xuqiu_file, index)" v-text="item"></li>
                            </ul>
                        </yd-flexbox>
                        <yd-flexbox direction="vertical">
                            <div class="range">协办提交物：</div>
                            <ul class="list">
                                <li class="file" v-for="(item, index) in submitAttach" @click="queryText(file_url.xieban_file, index)" v-text="item"></li>
                            </ul>
                        </yd-flexbox>
                    </div>
                </div>
            </div>
        </div>
        <div class="mainContent1" v-show="piyueFlag">
            <assist-approve v-if="liuzhuanNum > 0" :title="demandname" v-on:botSetflag="setFlag" :num="liuzhuanNum"></assist-approve>
        </div>
        <yd-tabbar v-if="$route.params.isDo == 0 && mainFlag" class="skip_box" slot="tabbar" bgcolor="#61b3f6" color="#fff" size=".3rem">
            <div class="skip_btn">
                <a v-if="approveFlag" @click="setFlag(0)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations, mapActions } from 'vuex';
import assistApprove from '../assist/assistApprove.vue';
export default {
    name: 'innerAssist',
    components: {
        assistApprove
    },
    data() {
        return {
            approveFlag: true,
            tabflag: true,
            isIos: false,
            textHidden: true,
            textHidden1: true,
            textHidden2: true,
            demandname: '',
            assistantdeptname: '', // 协办方
            assistantname: '', // 经办人
            resultdescribe: '', // 协办描述
            resultattch: [], // 处理结果附件
            personnames: '', // 协办部门
            demandside: '', // 需求部门
            assistdescribe: '', // 需求描述
            linkman: '', // 联系人
            phone: '', // 联系电话
            xiebanFlag: true, // 当协办处理单全部为空时隐藏
            xuqiuFlag: false,
            piyueFlag: false,
            liuzhuanNum: 0,
            mainFlag: false, // 显示主页面
            submitAttach: [], // 协办提交物
            requestAttach: [], // 需求附件
            servicetype: 0, // 1:评审,2:征求意见,3:会议通知,4:专业审查,5:专项支持,6:安全协查
            chuli_file: [], // 处理结果附件
            xuqiu_file: [], // 需求附件
            xieban_file: [], // 协办提交物附件
            file_url: {
                chuli_file: [], // 处理结果附件
                xuqiu_file: [], // 需求附件
                xieban_file: [] // 协办提交物附件
            }, // 附件数组
            line6flag: false,
            over1: false,
            line10flag: false,
            over2: false,
            fuyan: '',
            errorTime: 0
        }
    },
    mounted() {
        const vm = this;
        vm.setTitleName('内部协办');
        document.setTitle('内部协办');
        var setNavigationBarTitle = function(title) {
            UPCHAT.M.NAPI.setNavigationBarTitle(title);
        };
        setTimeout(function() {
            var title = '内部协办';
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(setNavigationBarTitle(title));
            }
        }, 200);
        vm.pageRender();
        vm.errorTime = 0;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getHandleInfo'])
    },
    methods: {
        ...mapMutations([
            'setHandleInfo', 'setTitleName'
        ]),
        ...mapActions([
            'SetHandleInfo'
        ]),
        pageRender() {
            const vm = this;
            if (vm.getHandleInfo.keepData != '' && vm.getHandleInfo.keepData.workId == vm.$route.params.workItemID) {
                vm.fuyan = vm.getHandleInfo.file_url1;
                vm.Render(vm.getHandleInfo.keepData);
                if (vm.getHandleInfo.file_url == []) {
                    console.log(222);
                    vm.loadFile(vm.resultattch, 'chuli_file');
                    vm.loadFile(vm.requestAttach, 'xuqiu_file');
                    vm.loadFile(vm.submitAttach, 'xieban_file');
                } else {
                    if (vm.getHandleInfo.file_url.chuli_file.length < vm.resultattch.length) {
                        vm.loadFile(vm.resultattch, 'chuli_file');
                        console.log(333);
                    }
                    if (vm.getHandleInfo.file_url.xuqiu_file.length < vm.requestAttach.length) {
                        vm.loadFile(vm.requestAttach, 'xuqiu_file');
                        console.log(444);
                    }
                    if (vm.getHandleInfo.file_url.xieban_file.length < vm.submitAttach.length) {
                        vm.loadFile(vm.submitAttach, 'xieban_file');
                        console.log(555);
                    } else {
                        vm.file_url = vm.getHandleInfo.file_url;
                        console.log(666);
                    }
                }
            } else {
                vm.$dialog.loading.open('加载中...');
                vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                    'type': vm.$route.params.type,
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
                }, function(res) {
                    if (res.isSuccess == 'false') {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.pageRender();
                        } else {
                            vm.$dialog.loading.close();
                            vm.$dialog.toast({
                                mes: '表单未加载成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    } else if (res.isSuccess == 'true') {
                        vm.$dialog.loading.close();
                        const data = res.jsonData;
                        vm.Render(data);
                    }
                }, function(res) {
                    if (res.response.status != 200) {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.pageRender();
                            console.log(vm.errorTime);
                        } else {
                            vm.$dialog.loading.close();
                            vm.$dialog.toast({
                                mes: '表单未加载成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    }
                });
                // 上一步留言
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                    'type': vm.$route.params.type,
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
                }, function(res) {
                    vm.fuyan = res.jsonData[0].neirong;
                    vm.setHandleInfo({ file_url1: vm.fuyan });
                }, function(res) {}, false);
            }
        },
        seeMore() {
            const vm = this;
            vm.$nextTick(() => {
                let oHeighta = vm.$refs.textBoxa.offsetHeight;
                let oHeight2 = vm.$refs.line10.offsetHeight;
                if (oHeighta > oHeight2) {
                    vm.line10flag = true;
                    vm.over2 = true;
                }
            });
        },
        Render(data) {
            /*
             * 渲染页面
             */
            // 判断是否已经处理
            const vm = this;
            if ((data.CurrectPersonID && data.CurrectPersonID != vm.userInfo.userid) || (data.CURRENTSTATE != 10 && vm.$route.params.isDo != 1)) {
                vm.$dialog.toast({
                    mes: '该流程已处理',
                    timeout: 2000,
                    callback: () => {
                        setTimeout(function() {
                            vm.$router.push('/handle');
                        })
                    }
                });
                return false;
            }
            vm.assistantdeptname = data.assistantdeptname;
            vm.resultdescribe = data.resultdescribe;
            vm.assistantname = data.assistantname;
            vm.personnames = data.personnames;
            vm.resultattch = data.resultattch;
            vm.requestAttach = data.requestAttach;
            vm.submitAttach = data.submitAttach;
            vm.servicetype = data.servicetype;
            vm.assistdescribe = data.assistdescribe;
            vm.linkman = data.linkman;
            vm.phone = data.phone.split('/');
            vm.demandside = data.demandside;
            vm.demandname = data.demandname;
            data.workId = vm.$route.params.workItemID;
            vm.setHandleInfo({ nextBtn: data.nextBtn, activityDefId: vm.$route.params.activeId, keepAlive: [], keepData: data });
            //  附件加载
            vm.loadFile(vm.resultattch, 'chuli_file');
            vm.loadFile(vm.requestAttach, 'xuqiu_file');
            vm.loadFile(vm.submitAttach, 'xieban_file');
            vm.mainFlag = true;
            if (vm.assistantdeptname == '' && vm.resultdescribe == '' && vm.resultattch.length == 0) {
                vm.xiebanFlag = false;
                vm.seeMore();
            }
            if (vm.xiebanFlag) {
                vm.$nextTick(() => {
                    let oHeight = vm.$refs.textBox.offsetHeight;
                    let oHeight1 = vm.$refs.line6.offsetHeight;
                    if (oHeight > oHeight1) {
                        vm.line6flag = true;
                        vm.over1 = true;
                    }
                });
            }
        },
        returnYijian(num) {
            let str = '';
            if (num == 1) {
                str = '评审';
            } else if (num == 2) {
                str = '征求意见';
            } else if (num == 3) {
                str = '会议通知';
            } else if (num == 4) {
                str = '专业审查';
            } else if (num == 5) {
                str = '专项支持';
            } else if (num == 6) {
                str = '安全协查';
            } else {
                str = '';
            }
            return str;
        },
        loadFile(files, fileName) {
            /*
             *  files 需要遍历的文件名称
             *  fileName  把获取到的url地址分类存起来
             */
            const vm = this;
            files.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm[fileName][index] = '.rar';
                    vm.file_url[fileName][index] = '.rar';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else if (item.indexOf('.zip') != -1) {
                    vm[fileName][index] = '.zip';
                    vm.file_url[fileName][index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': vm.$route.params.type,
                            'fileName': item,
                            'workTitle': vm.demandname,
                            'workItemID': vm.$route.params.workItemID,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm[fileName][index] = res.data.jsonData;
                        vm.file_url[fileName][index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url: vm.file_url });
                    }).catch(error => {
                        console.log(error);
                    });
                }
            });
        },
        page_jump(detailUrl) {
            const vm = this;
            if (vm.$route.params.isDO != 1) {
                if (detailUrl.fileType != '.pdf') {
                    let index1 = detailUrl.pdfUrl.lastIndexOf('.') + 1;
                    let index2 = detailUrl.pdfUrl.length;
                    let txtName = detailUrl.pdfUrl.substring(index1, index2);
                    if (txtName != 'pdf') {
                        window.location.href = detailUrl.pdfUrl;
                    } else {
                        var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                        window.location.href = urls;
                    }
                } else {
                    var urls2 = vm.isIOS == true ? detailUrl.pdfUrl : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    window.location.href = urls2;
                }
            }
        },
        queryText(fileArr, index) {
            const vm = this;
            if (!fileArr) {
                // vm.textDialog();
                vm.$dialog.toast({
                    mes: '附件较大，正在加载中，请稍候',
                    timeout: 1500
                });
            } else if (fileArr[index] == '.rar') {
                vm.$dialog.toast({
                    mes: '目前暂不支持rar文件查看',
                    timeout: 2000
                });
            } else {
                let detailUrl = fileArr[index];
                vm.page_jump(detailUrl);
            }
        },
        getLength(str) {
            let num = 0;
            for (let i of str) {
                if (/[0-9a-z]/i.test(i)) {
                    num = num + 0.6;
                } else {
                    num = num + 1;
                }
            }
            return Math.floor(num);
        },
        getText(str, len) {
            let num = this.getLength(str);
            return num > len ? (str.substring(0, len - 8) + '...') : str;
        },
        setFlag(type) {
            // 0流转日志 1是审批日志
            if (type == 0) {
                this.piyueFlag = true;
                this.mainFlag = false;
                this.liuzhuanNum++;
            } else if (type == 1) {
                this.mainFlag = false;
                this.piyueFlag = false;
            } else {
                this.mainFlag = true;
                this.piyueFlag = false;
            }
        }
    }
}
</script>
<style lang="less" scoped>
.handle_main .yd-flexbox-item {
    border-bottom: none;
}

.mainContent {
    height: auto;
}

.file {
    color: #4aaeff !important;
}

.jijian {
    background: #ff9805 !important;
    font-weight: 600;
}

.open,
.close,
.open1 {
    color: #4aaeff;
}

.close {
    line-height: .52rem;
}

.blue {
    position: relative;
    top: .04rem;
}

.text_box {
    padding-top: .15rem;
    padding-bottom: .25rem;
    border-bottom: .02rem dashed #bfbfbf;
    width: 100%;
    height: auto;
    position: relative;

    .open {
        position: absolute;
        right: -.12rem;
        bottom: .29rem;
        background: #fff;
        padding-right: 5em;
        font-size: .3rem;
        padding-left: .2rem;
        height: .35rem;

        &:before {
            margin-right: .12rem;
            color: #333;
            font-size: .3rem;
            background: #fff;
        }
    }
}

.openOver1 {
    line-height: .44rem !important;
}

.text_box .line6 {
    max-height: 3rem;
    overflow: hidden;
    display: block;
    height: auto;
}

.line6n {
    height: 3rem;
    overflow: hidden;
    display: block;
    position: absolute;
}

.text_box .line10 {
    max-height: 5.12rem;
    overflow: hidden;
    display: block;
    height: auto;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.line10n {
    height: 5.2rem;
    overflow: hidden;
    display: block;
    position: absolute;
}

.detail_text {
    font-size: .3rem;
    color: #333;
    line-height: .52rem;
    display: inline;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.top_ul {
    border-bottom: .02rem dashed #bfbfbf;
    padding-bottom: .3rem !important;
    width: 100%;
}

.circle_box {
    position: relative;

    &.top_ul:after,
    &.text_box:after {
        right: -.4rem;
    }

    &.top_ul:before,
    &.text_box:before {
        left: -.4rem;
    }

    &:after {
        content: '';
        width: .16rem;
        height: .16rem;
        background: #e2e2e2;
        bottom: -.08rem;
        right: -.28rem;
        border-radius: 50%;
        position: absolute;
    }

    &:before {
        content: '';
        width: .16rem;
        height: .16rem;
        background: #e2e2e2;
        bottom: -.08rem;
        left: -.28rem;
        border-radius: 50%;
        position: absolute;
    }
}

.other_content {
    width: 7.16rem;
    margin: .15rem auto;
    background: #fff;
    font-size: .3rem;
    padding-bottom: .3rem;
    border-radius: .1rem;

    h2 {
        font-size: .32rem;
        color: #000;
        text-indent: .12rem;
        margin: 0 .2rem;
        height: .9rem;
        line-height: .9rem;
        border-bottom: .02rem dashed #bfbfbf;
    }

    .yd-flexbox {
        padding: 0 .32rem 0 .33rem; // text-align: justify;
    }

    .range {
        margin-right: .3rem;
    }

    .range_txt {
        color: #333;
    }

    .list li {
        padding-top: .3rem;
        color: #333;
    }
}

h3 {
    font-size: .32rem;
    color: #333;
    line-height: .5rem;
    margin-left: .2rem;
    font-weight: normal;
    border-bottom: .02rem dashed #bfbfbf;
    margin-right: .2rem;
    padding-bottom: .25rem;
}

.xuqiu .other_content {
    padding-top: .24rem;
}

.xuqiu {
    .tab_ul {
        padding-left: .22rem;
        overflow: hidden;
        font-size: .28rem;
        color: #999;

        li {
            float: left;
            width: 1.84rem;
            height: .44rem;
            border-radius: .44rem;
            text-align: center;
            line-height: .44rem;
            margin-right: .36rem;
            background: #ededed;

            &.active {
                color: #fff;
                background: #55b3ff;
            }
        }
    }
}

.find_more {
    width: 7.16rem;
    margin: .12rem auto;
    height: .82rem;
    border: .02rem dashed #4aaeff;
    border-radius: .08rem;
    font-size: .3rem;
    color: #4aaeff;
    line-height: .82rem;
    text-align: center;
}

h3.fuyan {
    border: none;
    padding-bottom: 0;
}

.fuyan_content {
    padding-top: .24rem;
}

.fuyan_box {
    padding: 0 .18rem;
    height: .43rem;
    line-height: .43rem;
    display: inline-block;
    font-size: .24rem;
    color: #fff;
    background: #ff5252;
    border-radius: .06rem;
    margin-left: .22rem;
    font-weight: 600;
    margin-bottom: .2rem;
}
</style>


